<template>
	<div class="hello">
		<h2>{{msg}}</h2>
		<h2>{{msgfromfather}}</h2>
		<button v-on:click="onclickMe">Click!</button>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				msg: 'hello from component a!'
			};
		},
		props:['msgfromfather'],
		events:{
			'onAddnew':function(items){
				console.log('onAddnew-a',items);
			}
		},
		methods: {
			onclickMe: function() {
				console.log(this.msgfromfather);
				// 事件名,参数
				this.$emit('tellme',this.msg);
			}
		}
	}
</script>

<style>
	h2 {
		color: #42B983;
	}
</style>



