<template>
	<div id="app">
		<h2 v-text="explain"></h2>
		<h1 v-text="title"></h1>
		<input v-model="newItem" v-on:keyup.enter="addNew"/>
		<ul>
			<li v-for="item in items"
					v-bind:class="{finished:item.state}"
					v-on:click="toggleState(item)"
					v-on:dblclick="del(item)">
					{{item.label}}
			</li>
		</ul>
		<p>child tells me:{{childWords}}</p>
		<component-a msgfromfather='you live'
					 v-on:tellme='listenTo'></component-a>
	</div>
</template>
<script>
import Store from './store'
import ComponentA from './components/componetA.vue'
console.log(Store);
export default {
	data: function() {
		return {
			title: "this is a todo list",
			explain:"一个 vue 编写的案例",
			items:Store.fetch(),
			newItem:'', 
			childWords:''
		}
	},
	watch:{
		items:{
			handler:function(items){
				console.log(items);
				Store.save(items);
			},
			deep:true
		}
	},
	events:{
		'tellme':function(msg){
			this.childWords = msg;
		}
	},
	components :{ComponentA},
	methods: {
		listenTo:function(msg){
			this.childWords = msg;
		},
		toggleState:function(item){
			item.state = !item.state;
		},
		del:function(item){
			var index = 0;
		},
		addNew:function(){
			this.items.push({label:this.newItem,state:false});
			this.newItem = ''
			// 报错
			//this.$broadcast('onAddnew',this.items)
		}
	}
}
</script>
<style>
	.finished{
		text-decoration: underline;
	}
	body{
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 120px;
	}
</style>



